/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useState } from "react";
import axios from "axios";

const serverURL = process.env.UPLOAD_SERVER || "http://jadwal:4000";

const ImageUpload = ({ setLogo, disabled }) => {
  const [data, setFile] = useState({ name: "", path: "" });
  const handleChange = (e: any) => {
    const file = e.target.files[0];
    uploadFile(file);
  };

  const uploadFile = (file: any) => {
    const formData = new FormData();
    formData.append("file", file);
    axios
      .post(`${serverURL}/uploadimage`, formData)
      .then((res) => {
        setFile({
          name: res.data.filename,
          path: serverURL + res.data.path,
        });
        if (setLogo) {
          setLogo(serverURL + res.data.path);
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <div>
      <div style={{ marginTop: 20, marginBottom: 10 }}>
        <input
          disabled={disabled}
          type="file"
          name="file"
          onChange={handleChange}
        />
      </div>
    </div>
  );
};

export default ImageUpload;
